

@media (min-width:500px){
  #mainContainer{
    position: absolute;
    width:100%;
    height:100%;
    background-color: rgb(28, 97, 65);
    left: 0px;
    top:0px;
    display: flex;
    
  
  
    #SettingsOverlay{
      position: absolute;
      top: 0px;
      left: 0px;
      width:100%;
      height: 100%;
      background-color: rgba(0,0,0,0.4);
      z-index:30;
      animation: overlayAnimation 0.4s linear;
  
  
      #SettingsMain{
        position: relative;
        background: white;
        margin-top:100px;
        width:500px;
        height:70%;
        margin-left:auto;
        margin-right:auto;
        vertical-align: middle;
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        border-radius: 10px;
        
        #SettingsHeader{
          margin-left: auto;
          margin-right: auto;
          position: relative;
          text-align: center;
          width:300px;
          font-size: 30px;
  
        }
  
        #SettingsForm{
          margin-left:auto;
          margin-right:auto;
          position: relative;
          height:300px;
          width: 300px;
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
  
          #submitButton{
            width: 100px;
            border-radius: 5px;
            border: none;
            height:30px;
          }
  
          .settingPart{
            text-align: center;
  
            height:200px;
  
            .settingPartDescription{
              width:100%;
              font-size:20px;
              font-family: Arial, Helvetica, sans-serif;
  
              
            }
            .arrowContainer{
              display:flex;
              justify-content: center;
  
              .arrowOrLetter{
                display: flex;
                float:left;
                padding: 5px;
                span{
                  margin-top:auto;
                  margin-bottom: auto;
                }
              }
  
            }
          }
        }
  
  
  
  
      }
  
      
    }
  
  
    .centerContainer{
      position: relative;
      margin-top: auto;
      display: inline;
      width:500px;
      margin-bottom:auto;
      margin-left:auto;
      margin-right: auto;
     
      
   
      .scoreContainer{
        display: inline;
        top:0;
        left:0px;
        height:20px;
        position: absolute;
        color: orange;
        width:248px;
        z-index:20;
        height:500px;
      }
      #gameOverOverlay{
        position: absolute;
        top: 0px;
        left: 0px;
        height: 100%;
        width: 100%;
        z-index: 30;
        background: rgba(0,0,0,0.4);
        display:flex;
        animation: overlayAnimation 0.3s linear;
  
        #gameOverOverlayCenter{
          margin:auto;
          width: 200px;
          height: 300px;
          display: flex;
          justify-content:space-evenly;
          flex-direction: column; 
          background: #eee;
          border-radius: 10px;
  
          #gameOverText{
         
            text-align: center;
            vertical-align: middle;
            height:20px;
          }
          #scoreText{
            text-align: center;
            vertical-align: middle;
            height:20px;
          }
          #settingsButton{
            background: grey;
            width: 80%;
            margin-left: 10%;
            border-radius: 5px;
            height:30px;
          }
          #newGameButton{
            background: orange;
            width: 80%;
            margin-left: 10%;
            border-radius: 5px;
            height:30px;
          }
        }
      }
      .mirrorWall{
        border: 1px solid grey;
      }
  
      .solidWall{
        border: 1px solid red;
      }
      .gameContainer{
        
        position: relative;
        display: inline;
        float: left;
        width: 500px;
        height:500px;
        background-color: white;
       
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        flex-basis: 0%;
        
        
        .field{
         
          width : 25px;
          height: 25px;
          
       
        }
        .shortSnakeField{
          width : 23px;
          height: 23px;
          border: 1px solid rgb(220, 220, 220);
          background-color: lightblue;
          color: black;
          border-radius: 3px;
          text-align: center;
          vertical-align: middle;
  
        
        }
        .middleSnakeField{
          width : 23px;
          height: 23px;
          border: 1px solid white;
          color: white;
          text-align: center;
          vertical-align: middle;
          background-color: rgb(221, 25, 166);
          animation: snakeMiddleAnimation 0.2s linear;
        }
  
        .longSnakeField{
  
          width : 23px;
          height: 23px;
          border: 1px solid orange;
          color: orange;
          text-align: center;
          vertical-align: middle;
          background-color: rgb(63, 12, 12);
          animation: snakeLongAnimation 0.2s linear;
  
  
        }
        #snakeHead{
          width : 23px;
          height: 23px;
          border: 1px solid lightblue;
          background-color: green;
          border-radius:5px;
        }
  
        .elementField{
          width : 25px;
          height: 25px;
  
          border-radius: 15px;
  
        }
      }
    }
  
  }




}


