        .field{
         
          width : calc(95vw / 20) ;
          height:  calc(95vw / 20);
       
        }
        .shortSnakeField{
          width : calc((95vw / 20) - 2px);
          height:  calc((95vw / 20) - 2px);
          border: 1px solid rgb(220, 220, 220);
          background-color: lightblue;
          color: black;
          border-radius: 3px;
          text-align: center;
          vertical-align: middle;
  
        
