      }
    }
  
  }


}

@keyframes overlayAnimation {
    from{background:rgba(0,0,0,0)}
    to{background: rgba(0,0,0,0.4)}
}

@keyframes snakeMiddleAnimation{
  from{ color: rgb(221, 25, 166);}
  to{  color: white;}
}

@keyframes snakeLongAnimation{
  from{ color: rgb(63, 12, 12);}
  to{  color: orange;}

}